pmbTrans:before {
  position: absolute;
  top: calc(50% - 5px);
  left: calc(50% - 5px);
  width: 10px;
  height: 10px;
  background-image: linear-gradient(to top, #ED0677 0%, #ED0677 100%);
  z-index: -1;
  transition: all .2s linear;
  border-radius: 100%;
  opacity: 0;
}

pmbTrans:hover {
  color: white;
}

pmbTrans:hover:before {
  transform: scale(25);
  opacity: 1;
}

